:host {
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin-top: calc(-1 * var(--pfe-theme--container-spacer, 1rem));
  margin-right: calc(-1 * var(--pfe-theme--container-spacer, 1rem));
}

@media (min-width: 576px) {
  :host {
    flex-direction: row;
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  /* IE10+ */
  ::slotted(*) {
    max-width: 100%;
  }
}

:host([stacked]:not([stacked="false"])),
:host([stacked]) {
  flex-direction: column;
}

.pfe-icon-panel__content {
  margin-top: var(--pfe-theme--container-spacer, 1rem);
  margin-left: 0;
}

@media (min-width: 576px) {
  .pfe-icon-panel__content {
    margin-top: 0;
    margin-left: var(--pfe-theme--container-spacer, 1rem);
  }
}

.pfe-icon-panel__footer {
  margin-top: var(--pfe-theme--content-spacer, 1.5rem);
}
